import axios from "axios";

export const GET_RECIPES = "GET_RECIPES";

export function getRecipes() {
  return async function (dispatch) {
    try {
      let json = await axios.get("http://localhost:3001/recipe");
      console.log(json);
      return dispatch({
        type: GET_RECIPES,
        payload: json.data,
      });
    } catch (error) {
      console.log(error);
    }
  };
}
