import React from "react";
import { Link } from "react-router-dom";

export default function Card({ name, diet, image, id }) {
  return (
    <div>
      <Link to={`/recipe/${id}`}>
        <div>
          <div>
            <img src={image} alt="Img not found" />
          </div>
          <div>
            <h2>{name}</h2>
            <ul>
              {diet.map((d) => (
                <li key={d.name}>{d.name}</li>
              ))}
            </ul>
          </div>
        </div>
      </Link>
    </div>
  );
}
